import { GlassCard } from '@/components/ui/GlassCard';
import { ShieldAlert, ShieldCheck } from 'lucide-react';

export default function RiskPage() {
    return (
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 w-full">
            <h1 className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-alert-red via-orange-500 to-yellow-500 mb-8">
                RISK ANALYSIS
            </h1>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-12">
                <GlassCard className="p-6 border-alert-red/30 bg-alert-red/5">
                    <div className="flex items-center space-x-4 mb-4">
                        <ShieldAlert className="w-8 h-8 text-alert-red" />
                        <h2 className="text-2xl font-bold text-white">High Alert Objects</h2>
                    </div>
                    <p className="text-void-gray mb-4">
                        Objects with &gt;0.1% impact probability within the next 100 years.
                    </p>
                    <div className="text-4xl font-bold text-alert-red">2</div>
                </GlassCard>

                <GlassCard className="p-6 border-cyan-glow/30 bg-cyan-glow/5">
                    <div className="flex items-center space-x-4 mb-4">
                        <ShieldCheck className="w-8 h-8 text-cyan-glow" />
                        <h2 className="text-2xl font-bold text-white">Safe Passages</h2>
                    </div>
                    <p className="text-void-gray mb-4">
                        Objects passing safely within 5LD (Lunar Distances) this week.
                    </p>
                    <div className="text-4xl font-bold text-cyan-glow">42</div>
                </GlassCard>
            </div>

            <GlassCard className="p-6">
                <h3 className="text-xl font-bold mb-4">Detailed Risk Assessment</h3>
                <div className="overflow-x-auto">
                    <table className="w-full text-left text-sm text-void-gray">
                        <thead className="text-xs uppercase bg-white/5 text-white">
                            <tr>
                                <th className="px-4 py-3">Object ID</th>
                                <th className="px-4 py-3">Impact Prob.</th>
                                <th className="px-4 py-3">Velocity</th>
                                <th className="px-4 py-3">Diameter</th>
                                <th className="px-4 py-3">Next Approach</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-white/10">
                            <tr className="hover:bg-white/5 transition-colors">
                                <td className="px-4 py-3 font-medium text-white">99942 Apophis</td>
                                <td className="px-4 py-3 text-alert-red">Low (&lt; 1%)</td>
                                <td className="px-4 py-3">30.73 km/s</td>
                                <td className="px-4 py-3">370 m</td>
                                <td className="px-4 py-3">2029-04-13</td>
                            </tr>
                            <tr className="hover:bg-white/5 transition-colors">
                                <td className="px-4 py-3 font-medium text-white">101955 Bennu</td>
                                <td className="px-4 py-3 text-yellow-500">Minimal</td>
                                <td className="px-4 py-3">28.12 km/s</td>
                                <td className="px-4 py-3">490 m</td>
                                <td className="px-4 py-3">2135-09-24</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </GlassCard>
        </div>
    );
}
